import React from "react";
import Navbar from "./Navbar";
import pages from "./pages";
function App() {
  return (
    <>
      <header>
        <Navbar />
      </header>
      <main>
        {pages.map((section) => {
          return (
            <section
              className="section-center"
              id={section.text}
              key={section.id}
            >
              {section.text}
            </section>
          );
        })}
      </main>
    </>
  );
}

export default App;
